<template>
  <div class="home">
    <div>
      <keep-alive><router-view /></keep-alive>
    </div>

    <div>
      <van-tabbar v-model="tabName" class="nav">
        <van-tabbar-item
          v-for="(item,index) in homeDav"
          :key="index"
          :name="item.type"
          :icon="item.icon"
          @click="changeHome(item.type)"
        >{{item.title}}</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { homeDav } from "../data/homeDav.js";

export default {
  name: "home",

  data(){
    return {
      //底部导航栏的数据
      homeDav:[],

      tabName:'homePage',

      
    }
  },

  created(){

    this.homeDav = homeDav;
    this.tabName = this.$route.name

  },

  methods:{
    changeHome(type){
      this.$router.push({name: type})

    }
    
  }

};
</script>

<style lang="less" scoped>
.nav {
  background-color: #f5f5f5;
}
</style>
